
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import axios from 'axios'
export default {
components: {
Actionsheet
},
data() {
const userName = window.localStorage.getItem('userName')
const myId = window.localStorage.getItem("myId")
const friendId = this.$route.params.id
return {
userName,
show: false,
menus: {
'title.noop': '附加图片',
menu1: '拍一张',
menu2: '从相册中选择'
},
txt: '',
friendId,
myId,
unfinish: true
}
},
methods: {
click (key) {
console.log(key)
},
showPic () {
this.show = true
},
changeBtn () {
this.unfinish = false
if(this.txt ===''){
this.unfinish = true
}
},
finished () {
axios.get('http://121.42.177.97:8080/red/user/set_friend.do?user_id=' + this.myId + '&' + 'friend_id=' + this.friendId + '&' + 'friend_text=' + this.txt)
.then(res => {
window.localStorage.setItem("friend_text",this.txt)
this.$router.back()
})
.catch(res => {
throw res
})
}
}
}



